/* Navbar */
.navopened {
  background-color: #010606!important;
}
.navopened .logo {
  color: #fafafa!important;
}
.navopened ul li a {
  color: #fafafa!important;
}
.navopened .burger {
  background-color: #010606!important;
}
.navbar {
  transition: background-color 0.4s;
  position: fixed;
  top: 0;
  width: 100%;
  padding: 0.5rem;
  background-color: transparent;
  z-index: 2;
  backdrop-filter: blur(2px);
  display: flex; /* Use flexbox to arrange items */
  justify-content: space-between; /* Space between logo and menu */
  align-items: center;
}

/* Center the logo in the navbar */
.navbar .logo {
  display: flex;
  justify-content: flex-start; /* Keep logo aligned to the left */
  align-items: center; 
  color: rgb(243, 243, 243);
  letter-spacing: 2px;
  font-weight: 400;
  margin: 0;
  font-size: 28px;
  padding-top: 10px;
}

/* Ensure logo image fits properly */
.navbar .logo img {
  max-width: 100%;
  height: auto;
  max-height: 50px;
}

/* Navbar items */
.navbar ul {
  list-style: none;
  display: flex;
  justify-content: flex-end; /* Align the items to the far right */
  margin: 0;
  padding: 0;
  flex-grow: 1; /* Ensure it takes up remaining space */
  text-align: right;
}

.navbar ul li {
  margin: 0 1rem;
  display: inline-block;
}

.navbar ul li a {
  font-size: 20px;
  color: rgb(243, 243, 243);
  font-weight: 500;
  text-decoration: none;
  transition: all 0.2s;
  cursor: pointer;
}

/* Burger icon */
.button {
  cursor: pointer;
  transition: transform 0.2s linear;
  position: fixed;
  transform: translateX(300%);
  top: 20px;
  right: 15px;
  z-index: 1;
}

.burger {
  transition: all 0.2s;
  margin-bottom: 0.45rem;
  width: 25px;
  height: 3px;
  background-color: white;
}

/* Mobile responsiveness adjustments */
@media (max-width: 768px) {
  .navbar .logo img {
    max-height: 40px;
  }
  
  .navbar ul {
    list-style: none;
    display: block;
    text-align: right; /* Align the items to the right on mobile */
    margin: 0;
    padding-top: 2rem;
  }
  
  .navbar ul li {
    margin: 1rem 0;
  }
  
  .navbar ul li a {
    color: #fafafa;
  }
  
  .navbar {
    backdrop-filter: none;
    padding: 10px 0;
  }
  
  .navbar ul {
    backdrop-filter: blur(20px);
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    list-style: none;
    padding: 0;
    text-align: right;
    margin: 0;
    padding-top: 10rem;
    transform: translateX(-100%);
  }
  
  .navbar ul li {
    margin: 2rem 0rem;
    display: block;
  }
  
  .navbar ul li a {
    cursor: pointer;
    font-size: 20px;
    text-decoration: none;
    color: #fafafa;
    font-weight: 500;
  }
  
  .burger {
    background-color: #fafafa;
  }
}

